.icon-color {

    &--white {
        fill: $color-white;
    }

    &--mako {
        fill: $color-mako;
    }

    &--mischka {
        fill: $color-mischka;
    }

    &--brick-red {
        fill: $color-brick-red;
    }

    &--mine-shaft {
        fill: $color-mine-shaft;
    }

    &--ironside-gray {
        fill: $color-ironside-gray;
    }

    &--mountain-mist {
        fill: $color-mountain-mist;
    }
}
